import { MatButtonModule } from '@angular/material/button';
import { Component, OnInit } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { PodModel } from './PodModel';

export interface SearchType {
  name: string;
  city: string;
}

@Component({
  selector: 'pod-search',
  template: `
  <form class="search-form">
      <div class="row">
        <mat-form-field class="search-item" style="min-width: 282px;">
          <input type="text" placeholder="Search Hotels/City" aria-label="Search Hotels/City"
            matInput [formControl]="searchFormControl" name="searchText" 
             [matAutocomplete]="auto">
          <mat-autocomplete #auto="matAutocomplete" [displayWith]="displayFn">
          <mat-option *ngFor="let option of filteredOptions | async" [value]="option">
            {{option.name}}
          </mat-option>
          </mat-autocomplete>
        </mat-form-field>
        
        <mat-form-field class="search-item">
          <input matInput name="startDate" [matDatepicker]="startdate"
          [formControl]="startDateFormControl" placeholder="Start date">
          <mat-datepicker-toggle matSuffix [for]="startdate"></mat-datepicker-toggle>
          <mat-datepicker #startdate></mat-datepicker>
        </mat-form-field>

        <mat-form-field class="search-item">
          <input matInput name="endDate" [matDatepicker]="enddate" 
          [formControl]="endDateFormControl" placeholder="End date">
          <mat-datepicker-toggle matSuffix [for]="enddate"></mat-datepicker-toggle>
          <mat-datepicker #enddate></mat-datepicker>
        </mat-form-field>

        <div style="margin-top: 35px;">
          <button mat-raised-button color="primary" (click)="search()">Search</button>
          
        </div>



      </div>
  </form>

    
  `,
  styles: [`
  .example-button-row button,
.example-button-row a {
  margin-right: 8px;
}


  .search-form {
    min-width: 150px;
    max-width: 500px;
    width: 100%;
  }

  .search-item{
    margin: 0px 15px 0px 15px;
  }
  
  .search-full-width {
    width: 100%;
  }

  .row {
    display: flex;
    flex-direction: row;
    align-content: center;
  }

@media screen and (max-width: 600px) {
  .row {
    display: block;
  }
}
    
  `]
})
export class PodSearch {
  searchFormControl = new FormControl('', [
    Validators.required
  ]);

  startDateFormControl = new FormControl('', [
    Validators.required
  ]);

  endDateFormControl = new FormControl('', [
    Validators.required
  ]);

  options: SearchType[] = PodModel.search.suggestions;

  search() {
    let searchText = this.searchFormControl.value.name;
    let startDate = this.startDateFormControl.value;
    let endDate = this.endDateFormControl.value;

    //TODO: Convert to form group and check is valid
    if(!searchText || !startDate || !endDate)  return;

    //Removing time offset to fix timezone issue
    startDate= new Date( startDate.getTime() + Math.abs(startDate.getTimezoneOffset()*60000) );
    endDate= new Date( endDate.getTime() + Math.abs(endDate.getTimezoneOffset()*60000) );

    PodModel.search.userInput.searchText = searchText;
    PodModel.search.userInput.startDate = startDate.toISOString().substr(0,10);
    PodModel.search.userInput.endDate = endDate.toISOString().substr(0,10);

    console.log(PodModel.search.userInput);
  }

  filteredOptions: Observable<SearchType[]>;

  ngOnInit() {
    this.filteredOptions = this.searchFormControl.valueChanges
      .pipe(
        startWith(''),
        map(value => typeof value === 'string' ? value : value.name),
        map(name => name ? this._filter(name) : this.options.slice())
      );
  }

  displayFn(searchType?: SearchType): string | undefined {
    if(searchType){
      return searchType.name;
    }
    return undefined;
  }

  private _filter(name: string): SearchType[] {
    const filterValue = name.toLowerCase();

    return this.options.filter(option => option.name.toLowerCase().indexOf(filterValue) === 0);
  }

}